
.v-header img {
  width: 100%;
  height: 129px;
  display: block;
}
.v-header .nav ul {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  border: 1px solid #999;
  border-top: none;
}
.v-header .nav ul li {
    -webkit-box-flex: 1;
        -ms-flex: 1;
            flex: 1;
    font-size: 24px;
    text-align: center;
}
.v-header .nav ul li a {
      display: inline-block;
      text-decoration: none;
      height: 40px;
      line-height: 40px;
      color: black;
}
.v-header .nav ul li a i {
        vertical-align: 1px;
        margin-right: 6px;
}
.v-header .nav ul li a:hover {
        color: purple;
        cursor: hand;
}
.v-header .nav ul li a.router-link-active {
        color: purple;
}



