
.activity-index ul {
  text-align: center;
}
.activity-index ul li {
    display: inline-block;
    padding: 6px;
    margin: 4px;
    border: 1px solid black;
    border-radius: 10px;
}
.activity-index ul li a {
      text-decoration: none;
      color: black;
      /*font-weight: bold;*/
}
.activity-index ul li a:hover {
        cursor: pointer;
        color: purple;
}
.activity-index ul li a.router-link-active {
        color: purple;
        font-weight: bold;
}
.activity-content {
  width: 940px;
  margin: 0 auto 30px;
}
.activity-content p {
    line-height: 24px;
    text-indent: 2em;
    margin: 10px;
}
.activity-content .img {
    width: 100%;
    text-align: center;
}
.activity-content .img img {
      margin: 10px;
      width: 440px;
      height: 300px;
      /*border-radius:10px;*/
}



