
.right .research-name {
  margin-left: 40px;
}
.right .research-name h2 {
    text-align: left !important;
}
.right .research-name .content {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
}
.right .research-name .content p {
      width: 500px;
      text-indent: 2em;
      line-height: 24px;
      text-align: justify;
      display: inline-block;
}
.right .research-name .content .img {
      width: 220px;
      height: 150px;
      margin-left: 20px;
      vertical-align: top;
}
.right .research-name .content .img img {
        width: 100%;
        height: 100%;
}
.right .research-name .content.qita {
      display: block;
}
.right .research-name .content.qita .img {
        width: 800px;
        height: 100%;
}
.right .research-name .content.qita .img img {
          width: 45%;
          height: 600px;
          margin: 5px;
}
.right .research-people p {
  width: 500px;
  line-height: 24px;
  margin: 20px 0;
}



