
#mcnc {
  width: 1200px;
  height: 100%;
  margin: 0 auto;
}
#mcnc .top {
    position: absolute;
}
#mcnc .top {
    height: 170px;
    width: 1200px;
    top: 0;
}
#mcnc .bottom {
    height: 90px;
    width: 1198px;
    bottom: 0;
}
#mcnc .main {
    margin-top: 180px;
    min-height: 600px;
    margin-bottom: 10px;
    width: 1158px;
    padding: 0 20px;
    border: 1px solid #ccc;
}



