
.member_lab h2 {
  text-align: left !important;
}
.member_lab .description {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  border-top: 1px solid #ccc;
  padding: 30px 0 0 10px;
  margin: 40px 0 10px 10px;
}
.member_lab .description h2 {
    margin-top: 8px;
}
.member_lab .description img {
    border-radius: 10px 12px;
    width: 100px;
    height: 120px;
    margin: 0 20px 10px 10px;
}



